import React from "react";

const LinksCard = ({website, github, twitter, instagram, facebook, isPublic, loggedUserInfo, username}) => {
    return (
        <div className="card mt-3" style={{backgroundColor: "#f3f3f3"}}>
            {isPublic || loggedUserInfo.username === username ?
                <ul className="list-group list-group-flush">
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                    <h6 className="mb-0">
                        Website
                    </h6>
                    <span className="text-secondary">{website || ""}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                    <h6 className="mb-0">
                        Github
                    </h6>
                    <span className="text-secondary">{github || ""}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                    <h6 className="mb-0">
                        Twitter
                    </h6>
                    <span className="text-secondary">{twitter || ""}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                    <h6 className="mb-0">
                        Instagram
                    </h6>
                    <span className="text-secondary">{instagram || ""}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                    <h6 className="mb-0">
                        Facebook
                    </h6>
                    <span className="text-secondary">{facebook || ""}</span>
                </li>
            </ul>: <div className="card-body" style={{backgroundColor: "#f3f3f3"}}>    </div>}
            </div>
    )
}

export default LinksCard
